.client-container{margin-bottom:5%;justify-content:space-around;align-items:center;justify-content:center}.client-container .client{border-radius:10px;margin:calc(var(--element-margin)*10);margin-bottom:0;display:flex;flex-direction:column}.client-container .client>img{width:500px;height:300px}.client-container .client .gallary{max-width:80%;display:flex;flex-wrap:wrap}.client-container .client .gallary .image-wrapper{margin:12px;overflow:hidden}.client-container .client .gallary .image-wrapper img{margin:0;width:150px;height:100px;background-image:url(https://uploads-ssl.webflow.com/63beff5…/63e2afa…_Custom%20Size%20%E2%80%93%205.png);background-position:50%;background-size:cover}.client-container .client-heading{display:flex;flex-direction:column;align-items:center;justify-content:center}.client-container .client-heading h3{font-family:poppins;font-weight:300;color:var(--font-color-heading)}.client-container .client-heading :last-child{opacity:.8}