@charset "UTF-8";
@import url(./text.scss);
@import url('./flex.scss');
@import url('./button.scss');
@import url('./fonts.scss');

:root {
  --color-background: #000000;
  // f7a57f
  // 9376e6
  // 729efa
  // font
  --color-font-home:#030304;
  --sadf: #eabe1e96;
  --color-font-heading: #ffffff;
  --color-font-sub-heading: #ffffff;
  --color-font-content: #ffffff;

  // spacing
  --padding-page: 60px;
  --element-margin: 8px;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: end;
}

.line {
  margin-top: calc(var(--element-margin)*2);
  margin-bottom: calc(var(--element-margin)*2);
  background-color: var(--color-font-heading);
  height: 2px;
}

.line-vertical {
  border-left: 2px solid var(--color-font-heading);
}

.main {
}

.page {
  margin-bottom: 10%;
  padding: var(--padding-page);
}

.background-image {
  position: fixed;
  height: 100vh;
}

.grad1 {
  background: radial-gradient(circle at 90%, #6b89e9, #000000 40%, #000000 100%, #000000 75%);
}

.grad2 {
  background: radial-gradient(circle at 90%, #6be9e9, #000000 40%, #000000 100%, #000000 75%);
}
.main{
  
  transition: background-color 0.8s ease;
  background-color: var(--color-background);
}